.about {
  background: #F8FDFF;
}

.about-wrapper {
  position: relative;
  max-width: 1240px;
  margin: 0 auto;
  padding-bottom: 121px;

  @include vp-767 {
    padding-bottom: 78px;
  }

  @include vp-374 {
    padding-bottom: 81px;
  }
}


.year-picture {
  position: absolute;
  top: 57px;
  left: -5%;
  transform: rotate(270deg);

  @include vp-767 {
    top: 13%;
    left: -5%;
  }

  @media(max-width: 650px) {
    left: -12%;
  }

  @media(max-width: 500px) {
    display: none;
  }
}

.year-picture p {
  letter-spacing: -8px;
  font-weight: 700;
  font-size: 155px;
  line-height: 220px;
  color: #00B35D;
  display: inline;
  background-image: url("/img/background-text.png");
  background-size: cover;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-767 {
    font-size: 152px;
  }
}

.about__description {
  max-height: 240px;
  margin-left: 260px;
  overflow: hidden;

  @include vp-767 {
    margin-left: 210px;
    margin-right: 30px;
    max-height: 324px;
  }

  @media(max-width: 500px) {
    margin-left: 20px;
  }

  @include vp-374 {
    max-height: 263px;
    margin-left: 10px;
    margin-right: 0;
  }
}

.about__description ul {
  padding-left: 0;
  margin-bottom: 50px;


  @include vp-767 {
    margin-top: 0;
  }

  @include vp-374 {
    margin-top: 0;
  }
}

.about__description h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 32px;
  line-height: 48px;
  color: #041827;

  @include vp-374 {
    font-size: 25px;
    margin-bottom: 9px;
  }
}

.about__description li {
  list-style: none;
}

.about__description p {
  margin-bottom: 21px;
  font-family: $ff-pt_mono;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;

  @include vp-767 {
    margin-top: 0;
    line-height: 21px;
    margin-bottom: 24px;
  }

  @include vp-374 {
    margin-top: 0;
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 17px;
  }
}

.btn-about {
  margin-top: 16px;
  cursor: pointer;
  display: block;
  margin-right: auto;
  margin-left: 260px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  padding: 13px 49px;
  color: #00B2FF;
  border: 1px solid #00B2FF;
  border-radius: 3px;

  @include vp-767 {
    margin-left: 206px;
  }

  @media(max-width: 500px) {
    margin-left: 20px;
  }

  @include vp-374 {
    margin-top: 15px;
    padding: 9px 37px;
    margin-left: 9px;
  }

  &:hover {
    opacity: 0.8;
  }
}

.about__description.is-active {
  max-height: 100%;
}
