  margin-top: 15px;
  padding-left: 4px;
  font-family: "pt_mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.6;
  margin-left: 40px;
  user-select: none;
  cursor: pointer;
