  opacity: 1;
}

.form-checkbox input {
  width: 0;
  height: 0;
}
.form-checkbox input:checked::after {
  content: "";
}

.form-checkbox label + input::before {
  position: absolute;
  top: 22px;
  left: 20px;
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
@media (max-width: 767px) {
  .form-checkbox label + input::before {
    top: 16px;
    left: 20px;
  }
}
@media (max-width: 374px) {
  .form-checkbox label + input::before {
    left: 6px;
  }
}
.form-checkbox label + input::after {
  display: block;
  position: absolute;
  top: 26px;
  left: 24px;
  transform: rotate(130deg);
  width: 6px;
  height: 4px;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}
@media (max-width: 767px) {
  .form-checkbox label + input::after {
    top: 21px;
  }
}
@media (max-width: 374px) {
  .form-checkbox label + input::after {
    left: 10px;
  }
}

.form-checkbox label + input:focus-visible::before {
  border: 1px solid #000000;
}

